export default {
  methods: {
    handleError(response) {
      switch (response.status) {
        case 401:
          this.$message.error('');
          this.$store.commit("logout");
          break;
        case 404:
          this.$message.error('');
          break;
        case 422:
          this.$message.error('');
          break;
        case 500:
          this.$message.error('');
          break;
        default:
          break;
      }
    }
  }
}



